<script>
	import { Router, Link, Route } from "svelte-navigator";
	import { _ } from "svelte-i18n";
	import { onMount } from "svelte";
	import Header from "./Header.svelte";
	import Highscores from "./Highscores.svelte";
	import Blocks from "./Blocks.svelte";
	import Items from "./Items.svelte";
	import Mobs from "./Mobs.svelte";
	import Players from "./Players.svelte";

	let nav;

	onMount(() => {
		nav = document.getElementsByTagName("nav")[0];
	});

	function toggleNav(event) {
		if (event.detail.text !== "toggleNav") {
			return;
		}

		console.log(nav.style.display);

		if (nav.style.display == "none" || nav.style.display == "") {
			nav.style.setProperty("display", "block");
		} else {
			nav.style.setProperty("display", "none");
		}
	}
</script>

<svelte:head>
	<title>{$_("page_title")}</title>
</svelte:head>

<Header on:message={toggleNav} />
<Router>
	<nav>
		<img src="/images/logo.png" alt="avernin.one Logo" class="logo" />
		<div class="links">
			<Link to="/"><span>{$_("menu.highscores")}</span></Link>
			<Link to="blocks"><span>{$_("menu.blocks")}</span></Link>
			<Link to="items"><span>{$_("menu.items")}</span></Link>
			<Link to="mobs"><span>{$_("menu.mobs")}</span></Link>
			<Link to="players"><span>{$_("menu.players")}</span></Link>
		</div>
	</nav>

	<main>
		<Route path="blocks/*el" component={Blocks} />
		<Route path="items/*el" component={Items} />
		<Route path="mobs/*el" component={Mobs} />
		<Route path="players/*el" component={Players} />
		<Route path="/*el" component={Highscores} />
		<Route path="/" component={Highscores} />
	</main>
</Router>
<footer>
	built with <span class="heart">❤</span> by avernin
</footer>

<style lang="scss">
	nav {
		grid-area: Navigation;
		display: grid;
		grid-auto-flow: column;
		grid-template-rows: auto 1fr auto;
		background: var(--darkest);
		min-width: 10vw;
		box-shadow: 3px 0 7px -2px var(--black-line);

		img {
			margin: 3vh auto;
		}

		.links {
			:global(a) {
				margin: 0 1vw;
				display: block;
				margin-bottom: 1.5vh;
			}

			:global(a:hover span) {
				border-bottom: 2px solid var(--highlight);
				margin-bottom: -2px;
			}
		}
	}

	main {
		grid-area: Content;
		display: grid;
		grid-template-areas: "SideMenu MainContent";
		grid-template-columns: auto 1fr;
		height: 100%;
		overflow: hidden;
	}

	footer {
		grid-area: Footer;
		background: var(--black);
		line-height: 2em;
		font-size: 0.75em;
		text-align: center;

		.heart {
			color: var(--red);
		}
	}

	@media screen and (max-width: 600px) {
		nav {
			display: none;
		}
	}
</style>
