<script>
    import { _ } from "svelte-i18n";
    import { onMount, createEventDispatcher } from "svelte";

    const dispatch = createEventDispatcher();

    let buttonText;
    let currentTheme;

    const STORAGE_KEY = "theme";
    const DARK_PREFERENCE = "(prefers-color-scheme: dark)";

    const THEMES = {
        DARK: "dark",
        LIGHT: "light",
    };

    const prefersDarkThemes = () => window.matchMedia(DARK_PREFERENCE).matches;

    const applyTheme = () => {
        const preferredTheme = prefersDarkThemes() ? THEMES.DARK : THEMES.LIGHT;
        currentTheme = localStorage.getItem(STORAGE_KEY) ?? preferredTheme;

        if (currentTheme === THEMES.DARK) {
            buttonText = "🌕";
            document.body.classList.remove(THEMES.LIGHT);
            document.body.classList.add(THEMES.DARK);
        } else {
            buttonText = "🌑";
            document.body.classList.remove(THEMES.DARK);
            document.body.classList.add(THEMES.LIGHT);
        }
    };

    const toggleTheme = () => {
        const stored = localStorage.getItem(STORAGE_KEY);

        if (stored) {
            // clear storage
            localStorage.removeItem(STORAGE_KEY);
        } else {
            // store opposite of preference
            localStorage.setItem(
                STORAGE_KEY,
                prefersDarkThemes() ? THEMES.LIGHT : THEMES.DARK
            );
        }

        applyTheme();
    };

    onMount(() => {
        applyTheme();
        window
            .matchMedia(DARK_PREFERENCE)
            .addEventListener("change", applyTheme);
    });

    function toggleNav() {
        dispatch("message", {
            text: "toggleNav",
        });
    }
</script>

<header>
    <div class="title"><button on:click={toggleNav}>☰</button>averninstats</div>
    <div class="settings">
        <a href="https://discord.avernin.one/" class="link">Discord</a>
        <span>|</span>
        <a href="https://vote.avernin.one/" class="link">Vote</a>
        <span>|</span>
        <button on:click={toggleTheme}>{buttonText}</button>
    </div>
</header>

